/* global RTCRtpTransceiver */
var bowser = require('bowser');

var Logger = require('./Logger');

var Chrome75 = require('./handlers/Chrome75');

var Chrome70 = require('./handlers/Chrome70');

var Chrome67 = require('./handlers/Chrome67');

var Chrome55 = require('./handlers/Chrome55');

var Safari12 = require('./handlers/Safari12');

var Safari11 = require('./handlers/Safari11');

var Firefox60 = require('./handlers/Firefox60');

var Edge11 = require('./handlers/Edge11');

var ReactNative = require('./handlers/ReactNative');

var logger = new Logger('detectDevice');

module.exports = function () {
  // React-Native.
  if (typeof navigator === 'object' && navigator.product === 'ReactNative') {
    if (typeof RTCPeerConnection !== 'undefined') {
      return ReactNative;
    } else {
      logger.warn('unsupported ReactNative without RTCPeerConnection');
      return null;
    }
  } // browser.
  else if (typeof navigator === 'object' && typeof navigator.userAgent === 'string') {
      var ua = navigator.userAgent;
      var browser = bowser.getParser(ua);
      var engine = browser.getEngine(); // Chrome and Chromium.

      if (browser.satisfies({
        chrome: '>=75',
        chromium: '>=75'
      })) {
        return Chrome75;
      } else if (browser.satisfies({
        chrome: '>=70',
        chromium: '>=70'
      })) {
        return Chrome70;
      } else if (browser.satisfies({
        chrome: '>=67',
        chromium: '>=67'
      })) {
        return Chrome67;
      } else if (browser.satisfies({
        chrome: '>=55',
        chromium: '>=55'
      })) {
        return Chrome55;
      } // Opera.
      else if (browser.satisfies({
          opera: '>=57'
        })) {
          return Chrome70;
        } else if (browser.satisfies({
          opera: '>=44'
        })) {
          return Chrome55;
        } // Edge (Chromium based).
        else if (browser.satisfies({
            'microsoft edge': '>=75'
          })) {
            return Chrome75;
          } else if (browser.satisfies({
            'microsoft edge': '>=74'
          })) {
            return Chrome70;
          } // Old Edge with ORTC support.
          else if (browser.satisfies({
              'microsoft edge': '>=11'
            })) {
              return Edge11;
            } // Firefox.
            else if (browser.satisfies({
                firefox: '>=60'
              })) {
                return Firefox60;
              } // Safari with Unified-Plan support.
              else if (browser.satisfies({
                  safari: '>=12.1'
                }) && typeof RTCRtpTransceiver !== 'undefined' && RTCRtpTransceiver.prototype.hasOwnProperty('currentDirection')) {
                  return Safari12;
                } // Safari with Plab-B support.
                else if (browser.satisfies({
                    safari: '>=11'
                  })) {
                    return Safari11;
                  } // Best effort for Chromium based browsers.
                  else if (engine.name.toLowerCase() === 'blink') {
                      logger.debug('best effort Chromium based browser detection');
                      var match = ua.match(/(?:(?:Chrome|Chromium))[ /](\w+)/i);

                      if (match) {
                        var version = Number(match[1]);
                        if (version >= 75) return Chrome75;else if (version >= 70) return Chrome70;else if (version >= 67) return Chrome67;else return Chrome55;
                      } else {
                        return Chrome75;
                      }
                    } // Unsupported browser.
                    else {
                        logger.warn('browser not supported [name:%s, version:%s]', browser.getBrowserName(), browser.getBrowserVersion());
                        return null;
                      }
    } // Unknown device.
    else {
        logger.warn('unknown device');
        return null;
      }
};